@import 'variables';
@import 'mixins';
// @import 'typeplate-sk/typeplate';
@import 'tufte-css/et-bembo';
@import 'tufte-css/tufte';
@import 'navbar';
@import 'flexbox';

body {
    overflow-x: hidden;
    background-color: $offwhite;
}

p {
    font-size: 20px;
    &.small {
        font-size: 16px;
    }
}

a {
    outline: 0;
    text-decoration: none;
    color: $color_mountain_meadow_approx;
    &:hover {
        outline: 0;
        color: $color_mountain_meadow_approx;
    }
    &:focus {
        outline: 0;
        color: $color_mountain_meadow_approx;
    }
    &:active {
        outline: 0;
        color: $color_mountain_meadow_approx;
    }
    &.active {
        outline: 0;
        color: $color_mountain_meadow_approx;
    }
}
h1 {
    @extend %headers;
}
h2 {
    @extend %headers;
}
h3 {
    @extend %headers;
}
h4 {
    @extend %headers;
}
h5 {
    @extend %headers;
}
h6 {
    @extend %headers;
}
