.logo {line-height: 0;}
.img-headshot {
  height: 250px;
  clip-path: circle(95px at center 100px);
  -webkit-clip-path: circle(95px at center 100px);
  -moz-clip-path: circle(95px at center 100px);
}

.img-centered {
    margin: 0 auto;
}

header {
    text-align: center;
    color: $offwhite;
    // position: relative;
    background-color: $color_mountain_meadow_approx;
    background: url(../img/stickies.jpg) center center no-repeat fixed;
    // background: url(../img/star.png) center center no-repeat fixed;
    // background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/4273/starfield_stock_1.jpg) center center no-repeat fixed;
    background-size: cover;
    position: relative;
    transition: background-color .7s;
    &:before {
      @extend %curtains;
      transition: background-color .5s;
      background-color:  rgba(47, 154, 255, 0.93);
      //$color_mountain_meadow_approx
      // rgba(15, 84, 150, 0.94)
      z-index:0;
    }
    .container {
        padding-top: 100px;
        padding-bottom: 100px;
    }
    img {
        display: block;
        margin: 0 auto 20px;
    }
    .intro-text {
      position: relative;
        .name {
            display: inline-flex;
            justify-content: space-around;
            align-items: center;
            text-transform: uppercase;
            font-family: $font_0, $font_1, $font_2, $font_3, $font_4;
            font-size: 2em;
            font-weight: 700;
            .logo div {
              width:100px;
              margin: 20px;
            }
        }
        .skills {
            font-size: 1em;
            font-weight: 300;
            max-width: 30em;
            display: block;
            margin: 0 auto;
        }
    }
}
.marqueeMoon.midnight:before  {
  background: $color_pickled_bluewood_approx;
}


.row {
  display: flex;
  flex-flow: column;
  margin-left: -15px;
  margin-right: -15px;
}


.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

@media(min-width:768px) {
    header {
        .container {
            // padding-top: 200px;
            //padding-bottom: 100px;
        }
        .intro-text {
            .name {
                font-size: 4.75em;
            }
            .skills {
                font-size: 1.55em;
            }
        }
    }
    .navbar-fixed-top {
        padding: 1em 0;
        //Instead of the line below you could use @includetransition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
        transition: padding .3s;
        transition: background-color .5s;
        .navbar-brand {
            width: 9em;
            padding:0;
            //Instead of the line below you could use @includetransition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
            transition: all .3s;
        }
        &.navbar-shrink {
            padding: 10px 0;
            background-color: $color_pickled_bluewood_approx;
            .navbar-brand {
                width: 3.6em;
            }
        }
    }
}
.navbar {
    text-transform: uppercase;
    font-family: $font_0, $font_1, $font_2, $font_3, $font_4;
    font-weight: 100;
    .navbar-nav {
        letter-spacing: 1px;
        li a:focus {
            outline: 0;
        }
    }
    a:focus {
        outline: 0;
    }
}
.navbar-default {
    border: 0;
    background-color: transparent;
    .navbar-nav {
        li.active {
            a { border-bottom: 2px solid $offwhite;}
        }
    }
}

.navbar-inverse {
    border: 0;
}
section, main {
    padding: 100px 0;
    h2 {
        margin: 0;
        font-size: 3em;
    }
    &.mystery {
      background: $navy;
    }
    &.success {
        color: $white;
        background: $silly_puddy_purple;
        a {
            outline: 0;
            // color: $color_blue_bracket;
            &:hover {
                outline: 0;
                color: $color_pickled_bluewood_approx;
            }
            &:focus {
                outline: 0;
                color: $color_pickled_bluewood_approx;
            }
            &:active {
                outline: 0;
                color: $color_pickled_bluewood_approx;
            }
            &.active {
                outline: 0;
                color: $color_pickled_bluewood_approx;
            }
        }
    }
}
@media(max-width:767px) {
    .navbar-default {
      background-color: $color_pickled_bluewood_approx;
      .navbar-brand {
          width: 7em;
          padding: 8px 15px;
      }
    }
    section, main {
        padding: 75px 0;
        &.first {
            padding-top: 75px;
        }
    }
}
#portfolio {
    .portfolio-item {
        right: 0;
        margin: 0 0 15px;
        .portfolio-link {
            display: block;
            position: relative;
            margin: 0 auto;
            max-width: 400px;
            .caption {
                position: absolute;
                width: 100%;
                height: 100%;
                opacity: 0;
                background: $color_mountain_meadow_90_approx;
                //Instead of the line below you could use @includetransition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
                transition: all ease .5s;
                &:hover {
                    opacity: 1;
                }
                .caption-content {
                    position: absolute;
                    top: 50%;
                    width: 100%;
                    height: 20px;
                    margin-top: -12px;
                    text-align: center;
                    font-size: 20px;
                    color: $white;
                    i {
                        margin-top: -12px;
                    }
                    h3 {
                        margin: 0;
                    }
                    h4 {
                        margin: 0;
                    }
                }
            }
        }
    }
    * {
        z-index: 2;
    }
}
@media(min-width:767px) {
    #portfolio .portfolio-item {
        margin: 0 0 30px;
    }
}
.btn-outline {
    margin-top: 15px;
    border: solid 2px $white;
    font-size: 20px;
    color: $white;
    background: 0 0;
    //Instead of the line below you could use @includetransition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: all .3s ease-in-out;
    &:hover {
        @extend %activeButtons;
    }
    &:focus {
        @extend %activeButtons;
    }
    &:active {
        @extend %activeButtons;
    }
    &.active {
        @extend %activeButtons;
    }
}
.floating-label-form-group {
    position: relative;
    margin-bottom: 0;
    padding-bottom: .5em;
    border-bottom: 1px solid $color_gallery_approx;
    input {
        @extend %formz;
    }
    textarea {
        @extend %formz;
    }
    label {
        display: block;
        z-index: 0;
        position: relative;
        top: 2em;
        margin: 0;
        font-size: .85em;
        line-height: 1.764705882em;
        vertical-align: middle;
        vertical-align: baseline;
        opacity: 0;
        //Instead of the line below you could use @includetransition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
        transition: top .3s ease,opacity .3s ease;
    }
}
footer {
    color: $white;
    h3 {
        margin-bottom: 30px;
    }
    .footer-above {
        padding-top: 50px;
        background-color: $color_pickled_bluewood_approx;
    }
    .footer-col {
        margin-bottom: 50px;
    }
    .footer-below {
        padding: 25px 0;
        background-color: $color_ebony_clay_approx;
    }
}
.btn-social {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 2px solid $white;
    //Instead of the line below you could use @includeborder-radius($radius, $vertical-radius)
    border-radius: 100%;
    text-align: center;
    font-size: 20px;
    line-height: 45px;
}
.btn {
    &:focus {
        outline: 0;
    }
    &:active {
        outline: 0;
    }
    &.active {
        outline: 0;
    }
}
.scroll-top {
    z-index: 1049;
    position: fixed;
    right: 2%;
    bottom: 2%;
    width: 50px;
    height: 50px;
    .btn {
        width: 50px;
        height: 50px;
        //Instead of the line below you could use @includeborder-radius($radius, $vertical-radius)
        border-radius: 100%;
        font-size: 20px;
        line-height: 28px;
        &:focus {
            outline: 0;
        }
    }
}
.portfolio-modal {
    .modal-content {
        padding: 100px 0;
        min-height: 100%;
        border: 0;
        //Instead of the line below you could use @includeborder-radius($radius, $vertical-radius)
        border-radius: 0;
        text-align: center;
        //Instead of the line below you could use @includebackground-clip($clip)
        background-clip: border-box;
        //Instead of the line below you could use @includebox-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
        box-shadow: none;
        h2 {
            margin: 0;
            font-size: 3em;
        }
        img {
            margin-bottom: 30px;
        }
        .item-details {
            margin: 30px 0;
        }
    }
    .close-modal {
        position: absolute;
        top: 25px;
        right: 25px;
        width: 75px;
        height: 75px;
        background-color: transparent;
        cursor: pointer;
        &:hover {
            opacity: .3;
        }
        .lr {
            z-index: 1051;
            width: 1px;
            height: 75px;
            margin-left: 35px;
            background-color: $color_pickled_bluewood_approx;
            //Instead of the line below you could use @includetransform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
            transform: rotate(45deg);
            .rl {
                z-index: 1052;
                width: 1px;
                height: 75px;
                background-color: $color_pickled_bluewood_approx;
                //Instead of the line below you could use @includetransform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
                transform: rotate(90deg);
            }
        }
    }
    .modal-backdrop {
        display: none;
        opacity: 0;
    }
}
.floating-label-form-group::not(:first-child) {
    padding-left: 14px;
    border-left: 1px solid $color_gallery_approx;
}
.floating-label-form-group-with-value label {
    top: 0;
    opacity: 1;
}
.floating-label-form-group-with-focus label {
    color: $color_mountain_meadow_approx;
}
form .row:first-child .floating-label-form-group {
    border-top: 1px solid $color_gallery_approx;
}
