$main-font-color: $color_pickled_bluewood_approx;


html { font-size: 15px; }
// body {font-family: et-bembo, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;}

main { width: 87.5%;
       margin-left: auto;
       margin-right: auto;
       padding-left: 12.5%;
       font-family: et-bembo, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
      //  background-color: $offwhite;
       color: $main-font-color;
       max-width: 1400px;
       counter-reset: sidenote-counter; }

h1.tufte { font-weight: 400;
     margin-top: 4rem;
     margin-bottom: 1.5rem;
     font-size: 3.2rem;
     line-height: 1; }

h2.tufte { font-style: italic;
     font-weight: 400;
     margin-top: 2.1rem;
     margin-bottom: 0;
     font-size: 2.2rem;
     line-height: 1; }

h3.tufte { font-style: italic;
     font-weight: 400;
     font-size: 1.7rem;
     margin-top: 2rem;
     margin-bottom: 0;
     line-height: 1; }

p.subtitle { font-style: italic;
             margin-top: 1rem;
             margin-bottom: 1rem;
             font-size: 1.8rem;
             display: block;
             line-height: 1; }

table { overflow-x: scroll;
        width: 98%;
        clear: left;
        text-align: center;
        font-size: 1.2rem;
        line-height: 1.4;
        margin: 1.4rem 1%;
        border-collapse: separate;
        border-spacing: 0 5px;
        -webkit-font-feature-settings: 'tnum'; /* This is technically redundant */
        -moz-font-feature-settings: 'tnum';
        -ms-font-feature-settings: 'tnum'; }

table.table-alpha { }

table.table-beta { font-family: "Trebuchet MS", "Gill Sans", "Gill Sans MT", sans-serif; }

thead th { text-align: left;
           border-bottom: 1px solid #AAAAAA;
           font-weight: 400;
           border-collapse: separate;
           border-spacing: 5px 5px; }

td.text { text-align: left; }

td > div.number { width: 27px;
                  text-align: right;
                  display: inline-block; }

span.table-label { padding-top: 8px; } /* to maintain height relative to table header row */

table.booktabs { width: auto;
                 margin: 0 auto;
                 border-spacing: 0px;
                 border-top: 2px solid #333333;
                 border-bottom: 2px solid #333333; }

.booktabs th { border-bottom: 1px solid #333333;
               padding: 0.65ex 0.5em 0.4ex 0.5em;
               font-weight: normal;
               text-align: center; }

.booktabs th.cmid { border-bottom: 1px solid #666666; }

.booktabs th.nocmid { border-bottom: none; }

.booktabs tbody tr:first-child td { padding-top: 0.65ex; } /* add space between thead row and tbody */

.booktabs td { padding-left: 0.5em;
               padding-right: 0.5em;
               text-align: left; }

.booktabs caption { font-size: 90%;
                    text-align: left;
                    width: auto;
                    margin-left: auto;
                    margin-right: auto;
                    margin-top: 1ex;
                    caption-side: top; }

.booktabs tbody tr td.l { text-align: left !important; }
.booktabs tbody tr td.c { text-align: center !important; }
.booktabs tbody tr td.r { text-align: right !important; }

main {
  article { position: relative;
            padding: 5rem 0rem; }

  section { padding-top: 1rem;
            padding-bottom: 1rem; }

  p, ol, ul { font-size: 1.4rem; }

  p { line-height: 2rem;
      margin-top: 1.4rem;
      margin-bottom: 1.4rem;
      padding-right: 0;
      vertical-align: baseline; }
}


blockquote p { font-size: 1.1rem;
               width: 50%; }

blockquote footer { width: 50%;
                    text-align: right; }
main {
  ul { width: 45%;
       -webkit-padding-start: 5%;
       -webkit-padding-end: 5%;
       list-style-type: none; }

  li { padding: 0.5rem 0; }


  figure { max-width: 55%;
           -webkit-margin-start: 0;
           -webkit-margin-end: 0;
           margin-bottom: 3em; }

  figcaption { float: right;
               clear: right;
               margin-right: -48%;
               margin-top: 0;
               margin-bottom: 0;
               font-size: 1.0rem;
               line-height: 1.6;
               vertical-align: baseline;
               position: relative;
               max-width: 40%; }

  figure.fullwidth figcaption { margin-right: 24%; }

  a { color: $color_pickled_bluewood_approx;
      text-decoration: none;
      border-bottom: 1px solid #777;
      padding-bottom: 1px; }

  img { max-width: 100%; }

  .sidenote, .marginnote { float: right;
                           clear: right;
                           margin-right: -60%;
                           width: 50%;
                           margin-top: 0;
                           margin-bottom: 0;
                           font-size: 1.1rem;
                           line-height: 1.3;
                           vertical-align: baseline;
                           position: relative; }

  .table-caption { float:right;
                   clear:right;
                   margin-right: -60%;
                   width: 50%;
                   margin-top: 0;
                   margin-bottom: 0;
                   font-size: 1.0rem;
                   line-height: 1.6; }

  .sidenote-number { counter-increment: sidenote-counter; }

  .sidenote-number:after, .sidenote:before { content: counter(sidenote-counter) " ";
                                             font-family: et-bembo-roman-old-style;
                                             position: relative;
                                             vertical-align: baseline; }

  .sidenote-number:after { content: counter(sidenote-counter);
                           font-size: 1rem;
                           top: -0.5rem;
                           left: 0.1rem; }

  .sidenote:before { content: counter(sidenote-counter) " ";
                     top: -0.5rem; }

  p, footer, div.table-wrapper { width: 55%; }

  div.table-wrapper { overflow-x: scroll; }

}

@media screen and (max-width: 760px) { p, footer { width: 90%; }
                                       pre.code { width: 87.5%; }
                                       ul { width: 85%; }
                                       figure { max-width: 90%; }
                                       figcaption, figure.fullwidth figcaption { margin-right: 0%;
                                                                                 max-width: none; }
                                       blockquote p, blockquote footer { width: 90%; }}

.sans { font-family: "Gill Sans", "Gill Sans MT", Calibri, sans-serif;
        letter-spacing: .03em; }

.code { font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
        font-size: 1.125rem;
        line-height: 1.6; }

h1 .code, h2 .code, h3 .code { font-size: 0.80em; }

.marginnote .code, .sidenote .code { font-size: 1rem; }

pre.code { width: 52.5%;
           padding-left: 2.5%;
           overflow-x: scroll; }

.fullwidth { max-width: 90%; }

span.newthought { font-variant: small-caps;
                  font-size: 1.2em; }

input.margin-toggle { display: none; }

label.sidenote-number { display: inline; }

label.margin-toggle:not(.sidenote-number) { display: none; }

@media (max-width: 760px) {
  main {
    label.margin-toggle:not(.sidenote-number) { display: inline; }
    .sidenote, .marginnote { display: none; }
    .margin-toggle:checked + .sidenote,
    .margin-toggle:checked + .marginnote {
      display: block;
      float: left;
      left: 1rem;
      clear: both;
      width: 95%;
      margin: 1rem 2.5%;
      vertical-align: baseline;
      position: relative;
    }
    label { cursor: pointer; }
    pre.code { width: 90%;
               padding: 0; }
    .table-caption { display: block;
                     float: right;
                     clear: both;
                     width: 98%;
                     margin-top: 1rem;
                     margin-bottom: 0.5rem;
                     margin-left: 1%;
                     margin-right: 1%;
                     vertical-align: baseline;
                     position: relative; }
    div.table-wrapper, table, table.booktabs { width: 85%; }
    div.table-wrapper { border-right: 1px solid #efefef; }
    img { width: 100%; }
  }
}
